.social li {
    display: inline-block;
    padding: 10px;
}

.social li a {
    width: 32px;
    height: 32px;
    display: block;
    border: 1px solid blue;
    text-align: center;
    line-height: 30px;
    border-radius: 4px;
}

.paddingLeft {
    padding-left: 20px;
}

.contact {
    list-style: none;
    font-style: italic;
    font-weight: bold; 
}

.contact li {
    display: block;
    padding: 10px;
    position: relative;
}

.contact li i {
    width: 32px;
    height: 32px;
    text-align: center;
    line-height: 30px;
    border-radius: 4px;
}

.rectangleFooter {
    border: 1px solid green;
    padding: 20px;
}
